.creative-studio-section-1 .btn-switch {
  width: 330px;
  height: 140px;
  border-radius: 75px;
  border: 10px solid var(--Stroke, #e2e2e2);
}
.creative-studio-section-1 .carouselTicker__item {
  margin: 0 40px;
  float: left;
  text-align: center;
}

.creative-studio-section-3 .card-service {
  transition: all 0.3s ease-in-out;
}
.creative-studio-section-3 .card-service .service-number {
  text-align: center;
  -webkit-text-stroke-width: 2;
  -webkit-text-stroke-color: var(--tc-theme-primary);
  line-height: 100%; /* 120px */
  background: var(--tc-theme-primary);
  -webkit-background-clip: text;
  -webkit-text-stroke: 2px var(--tc-theme-primary);
  font-weight: 400;
  color: var(--tc-system-white) !important;
  font-optical-sizing: auto;
  paint-order: stroke fill;
}
.creative-studio-section-3 .card-service:hover {
  border-color: var(--tc-system-black) !important;
}
.creative-studio-section-3 .card-service:hover .service-number {
  color: var(--tc-theme-primary) !important;
}
.creative-studio-section-3 .card-service:hover .service-card-icon {
  transition: all 0.3s ease-in-out;
  transform: rotate(45deg);
  border-color: var(--tc-system-black) !important;
  background: #000000 !important;
}
.creative-studio-section-3 .card-service:hover .service-card-icon svg g {
  opacity: 1;
}
.creative-studio-section-3 .card-service:hover .service-card-icon svg path {
  fill: var(--tc-system-white) !important;
}

.creative-studio-section-6.bg-secondary-2 .accordion .card .card-header {
  background: #fcf8f3;
}
.creative-studio-section-6.bg-secondary-2 .accordion .card .collapse {
  --bs-card-bg: #fcf8f3 !important;
  background: #fcf8f3 !important;
}
.creative-studio-section-6.bg-secondary-2 .accordion .card .collapsing {
  background: #fcf8f3 !important;
}
.creative-studio-section-6 .accordion .card {
  transition: all 0.3s ease-in-out;
}
.creative-studio-section-6 .accordion .card .card-header {
  background: var(--tc-system-white);
}
.creative-studio-section-6 .accordion .card .card-header a {
  border-bottom: 1px solid transparent;
}
.creative-studio-section-6 .accordion .card .card-header a .arrow::before {
  transition: 0.3s;
  transform: rotate(0deg);
  font-family: bootstrap-icons;
  content: "\f2ea";
  color: var(--tc-theme-primary);
  font-size: 20px;
}
.creative-studio-section-6 .accordion .card .card-header a.collapsed {
  border-bottom: 1px solid #d9d9d9;
}
.creative-studio-section-6 .accordion .card .card-header a.collapsed .arrow {
  background-color: transparent;
}
.creative-studio-section-6 .accordion .card .card-header a.collapsed .arrow::before {
  transition: 0.3s;
  color: var(--tc-system-black);
  transform: rotate(180deg);
  content: "\f4fe";
}
.creative-studio-section-6 .accordion .card:first-child .card-header a {
  border-top: 1px solid #d9d9d9;
}
.creative-studio-section-6 .accordion .card .collapse.show {
  border-bottom: 1px solid var(--tc-system-black);
}

.progress,
.progress-stacked {
  --bs-progress-height: 14px;
  --bs-progress-font-size: 0.75rem;
  --bs-progress-bg: #e9e9e9;
  --bs-progress-border-radius: 20px;
  --bs-progress-box-shadow: var(--bs-box-shadow-inset);
  --bs-progress-bar-color: #fff;
  --bs-progress-bar-bg: var(--tc-theme-primary);
  --bs-progress-bar-transition: width 0.6s ease;
  display: flex;
  height: var(--bs-progress-height);
  overflow: hidden;
  font-size: var(--bs-progress-font-size);
  background-color: var(--bs-progress-bg);
  border-radius: var(--bs-progress-border-radius);
}

.card-team .card-team-img {
  display: flex;
  border-radius: 8px;
  background: #f1f1f1;
  min-height: 270px;
  max-width: 270px;
}
.card-team .social-group {
  transition: all 0.2s linear;
  padding: 8px 10px;
  background: var(--tc-system-white);
  border-radius: 8px;
  backdrop-filter: blur(10px);
}
@media (min-width: 1400px) {
  .card-team .social-group {
    opacity: 0;
  }
}
.card-team .social-group .icon {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  background: #fcf8f3;
  border-radius: 50%;
  transition: all 0.2s linear;
}
.card-team .social-group .icon svg {
  height: 20px;
  width: 20px;
}
.card-team .social-group .icon svg path {
  fill: var(--tc-system-black);
}
.card-team .social-group .icon:hover {
  background: var(--tc-theme-primary);
}
.card-team .social-group .icon:hover svg path {
  fill: var(--tc-system-white);
  transition-duration: 0.2s;
}
.card-team:hover .social-group {
  opacity: 1;
}

.social-group-contact {
  transition: all 0.2s linear;
  padding: 8px 10px;
  background: var(--tc-system-white);
  border-radius: 8px;
  backdrop-filter: blur(10px);
}
.social-group-contact .icon {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  background: #fcf8f3;
  border-radius: 50%;
  transition: all 0.2s linear;
}
.social-group-contact .icon svg {
  height: 20px;
  width: 20px;
}
.social-group-contact .icon svg path {
  fill: var(--tc-system-black);
}
.social-group-contact .icon:hover {
  background: var(--tc-theme-primary);
}
.social-group-contact .icon:hover svg path {
  fill: var(--tc-system-white);
  transition-duration: 0.2s;
}

.card-award:hover .icon-shape {
  background-color: var(--tc-theme-primary) !important;
}
.card-award:hover .icon-shape svg path {
  stroke: var(--tc-system-white) !important;
}

.card-case-study .card-content {
  transition: all 0.3s linear;
}
@media (min-width: 1400px) {
  .card-case-study .card-content {
    opacity: 0;
  }
}
.card-case-study::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.2s linear;
  border-radius: 16px;
  background: rgba(41, 41, 41, 0.7);
}
.card-case-study:hover::before {
  opacity: 1;
  transition: all 0.2s linear;
}
.card-case-study:hover .card-content {
  opacity: 1;
  transition: all 0.3s linear;
}

.pagination_item {
  background: var(--tc-system-white);
  color: var(--tc-system-black);
  border-radius: 8px !important;
  font-size: 20px !important;
  font-weight: 600 !important;
}
.pagination_item svg path {
  stroke: var(--tc-system-black) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item.active {
  background: var(--tc-theme-primary) !important;
  color: var(--tc-system-white) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item.active svg path {
  stroke: var(--tc-system-white) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item:hover {
  background: var(--tc-theme-primary) !important;
  color: var(--tc-system-white) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item:hover svg path {
  stroke: var(--tc-system-white) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item:focus {
  box-shadow: none;
  background-color: var(--tc-theme-primary) !important;
  color: var(--tc-system-white) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item:focus svg path {
  stroke: var(--tc-system-white) !important;
  transition: 0.3s ease-in-out;
}

.blog-item:hover {
  background: var(--tc-theme-primary) !important;
  color: var(--tc-system-white) !important;
  transition: 0.2s linear;
}

.creative-studio-portfolio-details-section-1 .social-icons .icon {
  transition: all 0.3s ease-in-out;
  background-color: var(--tc-system-white);
}
.creative-studio-portfolio-details-section-1 .social-icons .icon svg {
  height: 14px;
  width: 14px;
  fill: var(--tc-system-black);
}
.creative-studio-portfolio-details-section-1 .social-icons .icon:hover {
  background-color: var(--tc-theme-primary);
  border-color: var(--tc-theme-primary) !important;
}
.creative-studio-portfolio-details-section-1 .social-icons .icon:hover svg {
  fill: var(--tc-system-white);
}

textarea {
  background: var(--tc-system-white);
  padding: 0.375rem 0.75rem !important;
  min-height: 120px !important;
}
textarea:focus {
  padding: 0.375rem 0.75rem !important;
  background: var(--tc-system-white);
}