.creative-studio-section-1 {
    .btn-switch {
        width: 330px;
        height: 140px;
        border-radius: 75px;
        border: 10px solid var(--Stroke, #e2e2e2);
    }
    .carouselTicker__item {
        margin: 0 40px;
        float: left;
        text-align: center;
    }
}
.creative-studio-section-3 {
    .card-service {
        transition: all 0.3s ease-in-out;
        .service-number {
            text-align: center;
            -webkit-text-stroke-width: 2;
            -webkit-text-stroke-color: var(--tc-theme-primary);
            line-height: 100%; /* 120px */
            background: var(--tc-theme-primary);
            -webkit-background-clip: text;
            -webkit-text-stroke: 2px var(--tc-theme-primary);
            font-weight: 400;
            color: var(--tc-system-white) !important;
            font-optical-sizing: auto;
            paint-order: stroke fill;
        }
        &:hover {
            border-color: var(--tc-system-black) !important;
            .service-number {
                color: var(--tc-theme-primary) !important;
            }
            .service-card-icon {
                transition: all 0.3s ease-in-out;
                transform: rotate(45deg);
                border-color: var(--tc-system-black) !important;
                background: #000000 !important;
                svg {
                    g {
                        opacity: 1;
                    }
                    path {
                        fill: var(--tc-system-white) !important;
                    }
                }
            }
        }
    }
}
.creative-studio-section-6 {
    &.bg-secondary-2 {
        .accordion {
            .card {
                .card-header {
                    background: #fcf8f3;
                }
                .collapse {
                    --bs-card-bg: #fcf8f3 !important;
                    background: #fcf8f3 !important;
                }
                .collapsing {
                    background: #fcf8f3 !important;
                }
            }
        }
    }
    .accordion {
        .card {
            transition: all 0.3s ease-in-out;
            .card-header {
                background: var(--tc-system-white);
                a {
                    .arrow {
                        &::before {
                            transition: 0.3s;
                            transform: rotate(0deg);
                            font-family: bootstrap-icons;
                            content: '\F2EA';
                            color: var(--tc-theme-primary);
                            font-size: 20px;
                        }
                    }
                    border-bottom: 1px solid transparent;
                    &.collapsed {
                        border-bottom: 1px solid #d9d9d9;
                        .arrow {
                            background-color: transparent;
                            &::before {
                                transition: 0.3s;
                                color: var(--tc-system-black);
                                transform: rotate(180deg);
                                content: '\F4FE';
                            }
                        }
                    }
                }
            }
            &:first-child {
                .card-header {
                    a {
                        border-top: 1px solid #d9d9d9;
                    }
                }
            }

            .collapse {
                &.show {
                    border-bottom: 1px solid var(--tc-system-black);
                }
            }
        }
    }
}
.progress,
.progress-stacked {
    --bs-progress-height: 14px;
    --bs-progress-font-size: 0.75rem;
    --bs-progress-bg: #e9e9e9;
    --bs-progress-border-radius: 20px;
    --bs-progress-box-shadow: var(--bs-box-shadow-inset);
    --bs-progress-bar-color: #fff;
    --bs-progress-bar-bg: var(--tc-theme-primary);
    --bs-progress-bar-transition: width 0.6s ease;
    display: flex;
    height: var(--bs-progress-height);
    overflow: hidden;
    font-size: var(--bs-progress-font-size);
    background-color: var(--bs-progress-bg);
    border-radius: var(--bs-progress-border-radius);
}
.card-team {
    .card-team-img {
        display: flex;
        border-radius: 8px;
        background: #f1f1f1;
        min-height: 270px;
        max-width: 270px;
    }
    .social-group {
        transition: all 0.2s linear;
        padding: 8px 10px;
        background: var(--tc-system-white);
        border-radius: 8px;
        backdrop-filter: blur(10px);
        @media (min-width: 1400px) {
            opacity: 0;
        }
        .icon {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            width: 44px;
            height: 44px;
            background: #fcf8f3;
            border-radius: 50%;
            transition: all 0.2s linear;
            svg {
                height: 20px;
                width: 20px;
                path {
                    fill: var(--tc-system-black);
                }
            }
            &:hover {
                background: var(--tc-theme-primary);
                svg {
                    path {
                        fill: var(--tc-system-white);
                        transition-duration: 0.2s;
                    }
                }
            }
        }
    }
    &:hover {
        .social-group {
            opacity: 1;
        }
    }
}
.social-group-contact {
    transition: all 0.2s linear;
    padding: 8px 10px;
    background: var(--tc-system-white);
    border-radius: 8px;
    backdrop-filter: blur(10px);
    .icon {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 44px;
        height: 44px;
        background: #fcf8f3;
        border-radius: 50%;
        transition: all 0.2s linear;
        svg {
            height: 20px;
            width: 20px;
            path {
                fill: var(--tc-system-black);
            }
        }
        &:hover {
            background: var(--tc-theme-primary);
            svg {
                path {
                    fill: var(--tc-system-white);
                    transition-duration: 0.2s;
                }
            }
        }
    }
}
.card-award {
    &:hover {
        .icon-shape {
            background-color: var(--tc-theme-primary) !important;
            svg {
                path {
                    stroke: var(--tc-system-white) !important;
                }
            }
        }
    }
}
.card-case-study {
    .card-content {
        transition: all 0.3s linear;
        @media (min-width: 1400px) {
            opacity: 0;
        }
    }
    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all 0.2s linear;
        border-radius: 16px;
        background: rgba(41, 41, 41, 0.7);
    }
    &:hover {
        &::before {
            opacity: 1;
            transition: all 0.2s linear;
        }
        .card-content {
            opacity: 1;
            transition: all 0.3s linear;
        }
    }
}
.pagination_item {
    background: var(--tc-system-white);
    color: var(--tc-system-black);
    border-radius: 8px !important;
    font-size: 20px !important;
    font-weight: 600 !important;
    svg {
        path {
            stroke: var(--tc-system-black) !important;
            transition: 0.3s ease-in-out;
        }
    }
    &.active {
        background: var(--tc-theme-primary) !important;
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:hover {
        background: var(--tc-theme-primary) !important;
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:focus {
        box-shadow: none;
        background-color: var(--tc-theme-primary) !important;
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
}
.blog-item {
    &:hover {
        background: var(--tc-theme-primary) !important;
        color: var(--tc-system-white) !important;
        transition: 0.2s linear;
    }
}
.creative-studio-portfolio-details-section-1 {
    .social-icons {
        .icon {
            transition: all 0.3s ease-in-out;
            background-color: var(--tc-system-white);
            svg {
                height: 14px;
                width: 14px;
                fill: var(--tc-system-black);
            }
            &:hover {
                background-color: var(--tc-theme-primary);
                border-color: var(--tc-theme-primary) !important;
                svg {
                    fill: var(--tc-system-white);
                }
            }
        }
    }
}
textarea {
    background: var(--tc-system-white);
    padding: 0.375rem 0.75rem !important;
    min-height: 120px !important;
    &:focus {
        padding: 0.375rem 0.75rem !important;
        background: var(--tc-system-white);
    }
}
